import AdminLayout from "@/layouts/Admin/index.vue";
import { defineComponent } from "vue";
import SingleValuePlaceHolder from "@/components/PlaceHolders/SingleValuePlaceHolder/index.vue";
import { mapState, useStore } from "vuex";
import moment from "moment";
import ExecutionRowPlaceHolder from "@/components/PlaceHolders/ExecutionRowPlaceHolder/index.vue";
import { HttpRequestMethod } from "@/enums/HttpRequestMethod";
import HttpClient from "@/util/HttpClient";
import routes from "@/routes";
import { v4 as uuidv4 } from "uuid";

const openedRows: string[] = [];

export default defineComponent({
  name: "Dashboard",
  components: {
    AdminLayout,
    SingleValuePlaceHolder,
    ExecutionRowPlaceHolder,
  },
  setup() {
    const store = useStore();
    return {
      store,
      moment,
    };
  },
  data() {
    return {
      isLoadingData: false,
      errorText: "",
      error: false,
      stats: {} as any,
      openedRows,
      successRatio: "N/A",
    };
  },
  computed: {
    ...mapState("auth", ["authToken"]),
  },
  created() {
    if (!navigator.onLine) {
      routes.push({
        name: "home.offline",
        params: { route: this.$route.fullPath },
      });
    }
  },
  async mounted() {
    // Get Blocks
    try {
      this.isLoadingData = true;
      const stats = await this.getStats();
      this.stats = stats;
      this.successRatio = this.calculateSuccessRatio(stats.latest_runs_new);
    } catch (e: any) {
      this.error = true;
      this.errorText = HttpClient.getTextError(e);
    } finally {
      this.isLoadingData = false;
    }
  },
  methods: {
    gotoBoto(id: number) {
      routes.push({ name: "home.editor", params: { id, uuid: uuidv4() } });
    },
    getNodeClass(status: number) {
      let result = "neutral";
      if (status === -1) {
        result = "ko";
      } else if (status === 1) {
        result = "ok";
      }
      return result;
    },
    calculateSuccessRatio(executions: Array<any>) {
      let success = 0;
      for (const execution of executions) {
        if (execution.status !== -1) success++;
      }
      const ratio = Math.ceil((success / executions.length) * 100);
      const result = executions.length > 0 ? `${ratio}%` : "N/A";
      return result;
    },
    toggleRow(id: string) {
      const index = this.openedRows.indexOf(id);
      if (index > -1) {
        this.openedRows.splice(index, 1);
      } else {
        this.openedRows.push(id);
      }
    },
    async getStats() {
      return await HttpClient.request(
        `https://${process.env.VUE_APP_BOTO_API_DOMAIN}/api/my/stats`,
        this.authToken!,
        HttpRequestMethod.GET
      );
    },
  },
});
